@mixin linx ($link, $hover, $active, $bg) {
  color: $link;
  &:hover {
    color: $hover;
    background: $bg;
  }
  &:active {
    color: $active;
    background: $bg;
  }
}




$white: #ffffff;
$red: #E30512;
$grey: #C4C4C4;
$black: #000000;
$green: #5b8300;
$orange: #f07d10;
$darkbrown: #232323;

$blue: #62caf1;
$darkblue: #365e8c;
$maxWidth: 1440px;
$laptopWidth: 1140px;

$largeSize: 36px;
$bigSize: 28px;
$mediumSize: 24px;
$lightSize: 18px;

$laptopLargeSize: 30px;
$laptopBigSize: 24px;
$laptopMediumSize: 20px;
$laptopLightSize: 16px;

$mobileLargeSize: 26px;
$mobileBigSize: 20px;
$mobileMediumSize: 18px;
$mobileLightSize: 15px;

$emobileLargeSize: 24px;
$emobileBigSize: 18px;
$emobileMediumSize: 16px;
$emobileLightSize: 13px;

@mixin btn ($width) {
  width: $width;
  display: flex;
  height: 74px;
  align-items: center;
  justify-content: center;
  background: #365e8c;
  color: white;
  border-radius: 10px;
  font-size: 24px;
  transition: 0.3s;

  &:hover {
    background: $blue;
    color: $darkblue;
  }

  @media (max-width: 1600px) {
    font-size: $laptopMediumSize;
    height: 54px;
  }
  @media (max-width: 1200px) {
    font-size: $mobileMediumSize
  }

  @media (max-width: 992px) {
    width: $width - 160;
    height: 45px;
  }

}

@mixin bigMediaSize () {
  @media (max-width: 1600px) {
    font-size: $laptopBigSize;
  }

  @media (max-width: 1400px) {
    font-size: $mobileBigSize;
  }

  @media (max-width: 992px) {
    font-size: $emobileBigSize;
  }
}

@mixin medMediaSize () {
  @media (max-width: 1600px) {
    font-size: $laptopMediumSize;
  }
  @media (max-width: 1200px) {
    font-size: $mobileMediumSize;
  }
  @media (max-width: 992px) {
    font-size: $emobileMediumSize;
  }
}

@mixin lightMediaSize () {
  @media (max-width: 1600px) {
    font-size: $laptopLightSize;
  }
  @media (max-width: 1200px) {
    font-size: $mobileLightSize;
  }
  @media (max-width: 992px) {
    font-size: $emobileLightSize;
  }
}

