header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;

  @media (max-width: 1200px) {
    position: static;
    background: #C0C0C0;
    padding-bottom: 25px;
  }

  &.fixed{
    .header__menu{
      transform: translateX(100%);
    }
    .header__inner{
      width: calc(100%);
      padding-right: 108px;
      background: white;
      position: fixed;
      padding-bottom: 10px;

      @media (max-width: 1200px) {
        position: static;
      }
    }
    .header__top {
      border-top: 3px solid $black;
    }

    .arrow-top-img{
      color: $black;
    }

    .header__top-right{
      svg path{
        stroke: black;
      }
    }
    .header__top-left{
      svg path{
        fill: black;
      }
    }
    .header__bottom-left {
      border-top: 1px solid rgba(0, 0, 0, 0.6);
      border-bottom: 1px solid rgba(0, 0, 0, 0.6);
    }

    .header__bottom-right {
      border-top: 1px solid rgba(0, 0, 0, 0.6);
      border-bottom: 1px solid rgba(0, 0, 0, 0.6);
    }

    .header__menu{
      position: fixed;

    }

    .header__menu-button{
      position: fixed;
      right: 14px;
      top: 15px;
      display: block;
      //transform: translate(-91px, -17px);
      width: 50px;
      height: 50px;
      //background: url("../img/svg/Group 3.svg") center center no-repeat;
      z-index: 100;
    }

    .header__social{
      top: 60vh;
    }

    .header__menu-button .menu-trigger span{
      background-color: #000 !important;
    }
  }
}

body.open-menu{
  .fixed .header__menu{
    transform: translateX(0);
  }
  @media (max-width: 1200px) {
    .header__menu{
      left: calc(40% - 8px);
      transform: translateX(0);
      width: calc(60% + 8px);
    }

    .header__menu-button .menu-trigger span{
      background-color: #000 !important;
    }
  }

  @media (max-width: 768px) {
    .header__menu {
      left: 0;
      width: 100%;
    }
  }

}


.header{



  &__inner{
    display: flex;
    flex-direction: column;
    width: calc(60% - 8px);
    padding: 18px 30px 0px 25px;
    transition: 0.6s;

    @media (max-width: 1200px) {
      width: 100%;
    }

    @media (max-width: 768px) {
      padding: 15px 20px 0px 20px;
    }
  }

  &__top{
    display: flex;
    justify-content: space-between;
    border-top: 3px solid #FFFFFF;
    padding-top: 15px;
    padding-left: 3px;
    padding-bottom: 6px;
    transition: 0.6s;

    @media (max-width: 1200px) {
      padding-right: 45px;
    }

    &--menu{
      @media (min-width: 768px) {
        display: none;

      }
      @media (max-width: 768px) {
        padding-right: 0;
        width: 100%;
        border-bottom: 1px solid;
        border-color: black;

        .header__top-left {
          margin-right: 33%;

          path {
            fill: black;
          }
        }

        .header__top-right path{
          stroke: black;
        }

        .header__top-left{
          order: 10;
        }
      }
    }

  }

  &__top-left{
    svg{
      width: 115px;
      path{
        transition: 0.6s;
      }
    }
  }

  &__top-right{
    svg{
      width: 30px;
      path{
        transition: 0.6s;
      }
    }
  }

  &__bottom{
    display: flex;
    justify-content: space-between;
  }

  &__bottom-left{
    padding: 12px 0 17px;
    border-top: 1px solid rgba(255, 255, 255, 0.6);
    border-bottom: 1px solid rgba(255, 255, 255, 0.6);
    width: 48%;
    transition: 0.6s;
    a{
      font-size: 16px;
      color: white;
      transition: 0.6s;
    }

  }

  &__bottom-right{
    padding: 12px 0;
    border-top: 1px solid rgba(255, 255, 255, 0.6);
    border-bottom: 1px solid rgba(255, 255, 255, 0.6);
    width: 48%;
    text-align: right;
    transition: 0.6s;
    a{
      font-size: 16px;
      color: white;
      transition: 0.6s;
    }
  }

  &__menu{
    position: absolute;
    left: calc(60% - 8px);
    top: 0;
    width: calc(40% + 8px);
    padding: 34px 26px;
    background: white;
    transition: 0.6s;
    min-height: 100vh;

    @media (max-width: 1200px) {
      transform: translateX(100%);
      z-index: 1;
    }

    @media (max-width: 768px) {
      padding: 17px 20px;
    }
  }

  &__menu-button{
    display: none;

    @media (max-width: 1200px) {
      display: block;
      position: absolute;
      right: 30px;
      top: 20px;
      z-index: 100;

      .menu-trigger span{
        background-color: white !important;
      }
    }
    @media (max-width: 768px) {
      top: 15px;
      right: 20px;
    }
  }

  &__menu-lk{
    line-height: 21px;
    font-size: 16px;
    margin-bottom: 74px;
    display: block;
    color: $black;

    @media (max-width: 768px) {
      margin-top: 20px;
      text-align: center;
      margin-bottom: 27px;
    }
  }

  &__menu-phone{
    @media (min-width: 768px) {
      display: none;
    }

    max-width: 225px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    margin: auto;
    border: 1px solid #DDDDDD;
    padding: 14px;
    margin-bottom: 27px;

    &:before{
      content: "";
      display: inline-block;
      background: url("../img/svg/phone.svg") center center no-repeat;
      width: 20px;
      height: 20px;
      margin-right: 8px;
    }
  }

  &__menu-inner{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 100%;
    max-height: 580px;

    @media (min-height: 768px) {
      margin-top: 15vh;
    }

    @media (max-width: 768px) {
      flex-wrap: nowrap;
      text-align: center;
      max-height: inherit;
    }
  }

  &__menu-list-wrap{
    margin-bottom: 78px;
    width: 45%;

    @media (max-width: 768px) {
      width: 100%;
      margin-bottom: 28px;
    }

    &--social{
      order: 11;

      @media (min-width: 768px) {
        display: none;
      }

      .header__social{
        display: block;
        position: static;
      }

      .header__social-list{
        flex-direction: row;
        justify-content: center;
      }

      .header__social-link{
        margin: 0 15px;

        svg{
          width: 19px;
        }

        &--vk{
