  }

  &__menu-title{
    font-size: 32px;
    line-height: 36px;
    color: $black;

    @media (max-width: 768px) {
      font-size: 24px;
      line-height: 28px;
    }
  }

  &__menu-list{
    padding-left: 30px;
    padding-top: 15px;

    @media (max-width: 768px) {
      padding-left: 0;
    }
  }

  &__menu-item{

  }

  &__menu-link{
    font-size: 16px;
    line-height: 28px;
    color: #ACACAC;
    position: relative;
    transition: 0.3s;

    &:hover{
      color: $black;

      &:before {
        opacity: 1;
        transform: translate(46%, -50%);
      }
    }

    &:before {
      content: "";
      display: block;
      width: 16px;
      height: 21px;
      position: absolute;
      top: 53%;
      transform: translate(-15%, -50%);
      left: -35px;
      opacity: 0;
      transition: 0.3s;
      background: url(../img/svg/right-arrow-black.svg) 100% 49% no-repeat;
      background-size: contain
    }
  }

  &__menu-list-wrap{
    @media (max-width: 768px) {
      &:nth-of-type(2) {
        order: 10;
      }
    }
  }

  &__menu-contacts{
    font-size: 16px;
    line-height: 28px;
    color: #ACACAC;
    padding-left: 20px;

    @media (max-width: 768px) {
      padding-left: 0;
    }

    &:nth-of-type(1){
      padding-top: 20px;
    }

    a{
      font-size: 16px;
      line-height: 28px;
      color: #ACACAC;
    }
  }

  &__menu-list-wrap{

  }

  &__menu-title{

  }

  &__menu-list{

  }

  &__menu-item{

  }

  &__menu-link{

  }

  &__menu-list-wrap{

  }

  &__menu-title{

  }

  &__menu-list{

  }

  &__menu-item{

  }

  &__menu-link{

  }

  &__social{
    position: fixed;
    top: 62vh;
    margin-top: 4px;
    right: 28px;
    transition: 0.6s;

    @media (max-width: 1200px) {
      display: none;
    }
  }

  &__social-list{
    display: flex;
    flex-direction: column;
  }

  &__social-link{
    margin-bottom: 29px;
    color: $black;
    transition: 0.3s;

    &:hover{
      color: #0029FF;
    }

    &--tg{
      svg{
        width: 12px;
      }
    }
    &--vk{
      margin-bottom: 35px;
      svg{
        width: 16px;
      }
    }
    &--inst{
      svg{
        width: 12px;
      }
    }
    &--fb{
      svg{
        width: 12px;
      }
    }
  }

}


.full-block{
  position: relative;
  height: 100vh;
  transition: 0.6s;

  @media (max-width: 1200px) {
    height: 627px;
  }

  &.hide-top{
    margin-top: -100vh;

    .full-block__top, .full-block__slider{
      width: 100%;
    }
  }

  &__top{
    position: relative;
    z-index: 1;
    text-align: right;
    width: calc(60% - 8px);
    padding: 133px 29px 0;
    color: white;
    transition: 0.6s;

    @media (max-width: 1200px) {
      width: 100%;
    }

    @media (max-width: 768px) {
      display: none;
    }
  }

  &__top_line{
    padding-bottom: 10px;
  }

  &__slider{
    height: 100%;
    position: absolute;
    top: 0;
    //width: 60%;
    width: calc(60% - 8px);
    transition: 0.6s;

    @media (max-width: 1200px) {
      width: 100%;
    }

    @media (max-width: 768px) {
      height: auto;
    }

    &:before{
      content: "";
      display: block;
      width: 84px;
      right: 28px;
      position: absolute;
      top: 46vh;
      height: 1px;
      background: rgba(255, 255, 255, 0.61);
      z-index: 1;
      margin-top: 5px;

      @media (max-width: 1200px) {
        top: 360px;
      }
    }

    &:after{
      content: "";
      display: block;
      width: calc(100% - 53px);
      height: 3px;
      background: white;
      position: absolute;
      bottom: 20px;
      margin: 0 30px 0 26px;
    }
  }

  &__slider-item{
    height: 100vh;
    background-size: cover !important;
    padding: 46vh 28px 0;

    @media (max-width: 1200px) {
      height: 627px;
      padding: 360px 28px 0;
    }

    //@media (max-width: 560px) {
    //  background-size: 130% auto !important;
    //}

    &:before{
      content: "";
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.04);
      z-index: 0;
    }

    div{
      position: relative;
      z-index: 1;
    }
  }

  &__slider-caption{
    margin-top: 5px;
    color: $white;
    width: calc(100% - 117px);
    border-top: 1px solid #999999;
    border-top: 1px solid rgba(255, 255, 255, 0.62);
    padding-top: 20px;
  }

  &__title{
    font-size: 48px;
    line-height: 50px;
    margin-top: 15px;
    color: $white;

    @media (max-width: 1200px) {
      font-size: 32px;
      line-height: 32px;
    }
  }

  &__number{
    letter-spacing: 10px;
    font: 180px/56px 'Poppins-Bold';
    font-weight: 100;
    margin-top: 75px;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-stroke: 1px $white;

    @media (max-width: 1200px) {
      font-size: 72px;
      line-height: 56px;
      margin-top: 25px;
    }
  }

}

