  font-size: 12px;
  line-height: 24px;
  text-align: center;
  border: 1px solid #999999;
  border-radius: 5px;
  color: #585757;
}



body {
  margin: 0;
  padding: 0;
}

button:focus {
  outline: none;
}

.more {
  position: relative;
  font-size: 12px;
  display: block;
  margin: auto;
  padding: 15px 0;
  width: 116px;
  border: 1px solid #999999;
  color: #585757;
  background: white;
  border-radius: 5px;
  cursor: pointer;
  -webkit-transition: .3s ease;
  -moz-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;

  @media (max-width: 560px) {
    width: 100%;
  }
}

.more:after {
  content: '';
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  border: solid 2px;
  border-color: #999999 transparent transparent transparent;
  border-radius: 100%;
  -webkit-transition: .3s ease;
  -moz-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;
  animation: spin 1s infinite;
  transform-origin: 0 0;
}

.more.load {
  padding: 15px 6px;
}

.more.load span {
  opacity: 0;
}

.more.load:after {
  display: block;
}

@keyframes spin {
  from { transform: rotate(0) translate(-50%, -50%) }
  to { transform: rotate(360deg) translate(-50%, -50%) }
}

.header__menu-button{
  //.menu-trigger {
  //  margin-right: 70px;
  //  margin-bottom: 50px;
  //}
  .menu-trigger,
  .menu-trigger span {
    display: inline-block;
    transition: all .4s;
    box-sizing: border-box;
  }

  .menu-trigger {
    position: relative;
    width: 28px;
    height: 18px;
    margin-left: 11px;
    margin-top: 17px;
  }

  .menu-trigger span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #000;
    border-radius: 4px;
  }

  .menu-trigger span:nth-of-type(1) {
    top: 0;
  }

  .menu-trigger span:nth-of-type(2) {
    top: 7px;
  }

  .menu-trigger span:nth-of-type(3) {
    bottom: 0;
  }




  .menu-trigger.type7 span:nth-of-type(1) {
    -webkit-animation : menu-bar07-01 .75s forwards;
    animation : menu-bar07-01 .75s forwards;
  }
  @-webkit-keyframes menu-bar07-01 {
    0% {
      -webkit-transform : translateY(8px) rotate(45deg);
    }
    50% {
      -webkit-transform : translateY(8px) rotate(0);
    }
    100% {
      -webkit-transform : translateY(0) rotate(0);
    }
  }
  @keyframes menu-bar07-01 {
    0% {
      transform : translateY(8px) rotate(45deg);
    }
    50% {
      transform : translateY(8px) rotate(0);
    }
    100% {
      transform : translateY(0) rotate(0);
    }
  }
  .menu-trigger.type7 span:nth-of-type(2) {
    transition : all .25s .25s;
    opacity : 1;

    @media (max-width: 1200px) {
      transition: all .4s;
    }

  }
  .menu-trigger.type7 span:nth-of-type(3) {
    -webkit-animation : menu-bar07-02 .75s forwards;
    animation : menu-bar07-02 .75s forwards;
  }
  @-webkit-keyframes menu-bar07-02 {
    0% {
      -webkit-transform : translateY(-8px) rotate(-45deg);
    }
    50% {
      -webkit-transform : translateY(-8px) rotate(0);
    }
    100% {
      -webkit-transform : translateY(0) rotate(0);
    }
  }
  @keyframes menu-bar07-02 {
    0% {
      transform : translateY(-8px) rotate(-45deg);
    }
    50% {
      transform : translateY(-8px) rotate(0);
    }
    100% {
      transform : translateY(0) rotate(0);
    }
  }
  .menu-trigger.active-7 span:nth-of-type(1) {
    -webkit-animation : active-menu-bar07-01 .75s forwards;
    animation : active-menu-bar07-01 .75s forwards;
  }
  @-webkit-keyframes active-menu-bar07-01 {
    0% {
      -webkit-transform : translateY(0) rotate(0);
    }
    50% {
      -webkit-transform : translateY(8px) rotate(0);
    }
    100% {
      -webkit-transform : translateY(8px) rotate(45deg);
    }
  }
  @keyframes active-menu-bar07-01 {
    0% {
      transform : translateY(0) rotate(0);
    }
    50% {
      transform : translateY(8px) rotate(0);
    }
    100% {
      transform : translateY(8px) rotate(45deg);
    }
  }
  .menu-trigger.active-7 span:nth-of-type(2) {
    opacity : 0;
  }
  .menu-trigger.active-7 span:nth-of-type(3) {
    -webkit-animation : active-menu-bar07-02 .75s forwards;
    animation : active-menu-bar07-02 .75s forwards;
  }
  @-webkit-keyframes active-menu-bar07-02 {
    0% {
      -webkit-transform : translateY(0) rotate(0);
    }
    50% {
      -webkit-transform : translateY(-8px) rotate(0);
    }
    100% {
      -webkit-transform : translateY(-8px) rotate(-45deg);
    }
  }
  @keyframes active-menu-bar07-02 {
    0% {
      transform : translateY(0) rotate(0);
    }
    50% {
      transform : translateY(-8px) rotate(0);
    }
    100% {
      transform : translateY(-8px) rotate(-45deg);
    }
  }

}


