/* Font */


@font-face {
  font-family: 'IBMPlexSans';
  src: url('../fonts/IBMPlexSans/IBMPlexSans.eot?#iefix') format('embedded-opentype'),  url('../fonts/IBMPlexSans/IBMPlexSans.woff') format('woff'), url('../fonts/IBMPlexSans/IBMPlexSans.ttf')  format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Poppins-Regular';
  src: url('../fonts/Poppins-Regular/Poppins-Regular.eot?#iefix') format('embedded-opentype'),
  url('../fonts/Poppins-Regular/Poppins-Regular.woff') format('woff'), url('../fonts/Poppins-Regular/Poppins-Regular.ttf')  format('truetype'), url('../fonts/Poppins-Regular/Poppins-Regular.svg#Poppins-Regular') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Poppins-Bold';
  src: url('../fonts/Poppins-Bold/Poppins-Bold.eot?#iefix') format('embedded-opentype'),
  url('../fonts/Poppins-Bold/Poppins-Bold.woff') format('woff'), url('../fonts/Poppins-Bold/Poppins-Bold.ttf')  format('truetype'), url('../fonts/Poppins-Bold/Poppins-Bold.svg#Poppins-Bold') format('svg');
  font-weight: normal;
  font-style: normal;
}




